
import { useState, useEffect } from 'react'
import { supabase } from '../supabaseClient'
import { treatiesData as staticTreaties, casesData as staticCases } from '../data'

export function useContent() {
    const [treaties, setTreaties] = useState(staticTreaties)
    const [cases, setCases] = useState(staticCases)
    const [resolutions, setResolutions] = useState([])
    const [loading, setLoading] = useState(true)

    useEffect(() => {
        const fetchData = async () => {
            try {
                // Fetch Treaties
                const { data: remoteTreaties, error: treatiesError } = await supabase
                    .from('treaties')
                    .select('*')
                    .order('created_at', { ascending: false })

                if (!treatiesError && remoteTreaties.length > 0) {
                    // Combine Static + Remote (or replace, depending on strategy)
                    // Here we append remote ones to static ones for now
                    // Or simpler: We prefer remote if available, but to keep static data visible without migration:
                    setTreaties([...remoteTreaties, ...staticTreaties])
                }

                // Fetch Cases
                const { data: remoteCases, error: casesError } = await supabase
                    .from('cases')
                    .select('*')
                    .order('created_at', { ascending: false })

                if (!casesError && remoteCases.length > 0) {
                    setCases([...remoteCases, ...staticCases])
                }

                // Fetch Resolutions
                const { data: remoteResolutions, error: resolutionsError } = await supabase
                    .from('resolutions')
                    .select('*')
                    .order('created_at', { ascending: false })

                if (!resolutionsError && remoteResolutions.length > 0) {
                    setResolutions(remoteResolutions)
                }

            } catch (error) {
                console.error("Error fetching content:", error)
            } finally {
                setLoading(false)
            }
        }

        fetchData()

        // Realtime Subscriptions
        const treatiesSub = supabase.channel('public:treaties')
            .on('postgres_changes', { event: 'INSERT', schema: 'public', table: 'treaties' }, payload => {
                setTreaties(prev => [payload.new, ...prev])
            })
            .subscribe()

        const casesSub = supabase.channel('public:cases')
            .on('postgres_changes', { event: 'INSERT', schema: 'public', table: 'cases' }, payload => {
                setCases(prev => [payload.new, ...prev])
            })
            .subscribe()

        const resolutionsSub = supabase.channel('public:resolutions')
            .on('postgres_changes', { event: 'INSERT', schema: 'public', table: 'resolutions' }, payload => {
                setResolutions(prev => [payload.new, ...prev])
            })
            .subscribe()

        return () => {
            supabase.removeChannel(treatiesSub)
            supabase.removeChannel(casesSub)
            supabase.removeChannel(resolutionsSub)
        }
    }, [])

    const isNew = (dateString) => {
        if (!dateString) return false
        const date = new Date(dateString)
        const now = new Date()
        const diffTime = Math.abs(now - date)
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
        return diffDays <= 30
    }

    return { treaties, cases, resolutions, loading, isNew }
}
