import { useState } from 'react'
import {
    Box, Heading, VStack, FormControl, FormLabel, Input, Textarea,
    Select, Button, useToast, Tabs, TabList, TabPanels, Tab, TabPanel,
    Container
} from '@chakra-ui/react'
import { supabase } from '../supabaseClient'
import { useUser } from '../context/UserContext'

export function AdminPage() {
    const { user } = useUser()
    const toast = useToast()
    const [loading, setLoading] = useState(false)

    // Treaty Form State
    const [treaty, setTreaty] = useState({
        name: '', date: '', topic: ' ', states: '', link: '', description: ''
    })

    // Case Form State
    const [caseData, setCaseData] = useState({
        name: '', year: '', court: 'ICJ', summary: '', link: ''
    })

    const handleTreatySubmit = async (e) => {
        e.preventDefault()
        setLoading(true)
        const { error } = await supabase.from('treaties').insert([treaty])
        setLoading(false)

        if (error) {
            toast({ title: '', description: error.message, status: 'error' })
        } else {
            toast({ title: '   !', status: 'success' })
            setTreaty({ name: '', date: '', topic: ' ', states: '', link: '', description: '' })
        }
    }

    const handleCaseSubmit = async (e) => {
        e.preventDefault()
        setLoading(true)
        const { error } = await supabase.from('cases').insert([caseData])
        setLoading(false)

        if (error) {
            toast({ title: '', description: error.message, status: 'error' })
        } else {
            toast({ title: '   !', status: 'success' })
            setCaseData({ name: '', year: '', court: 'ICJ', summary: '', link: '' })
        }
    }

    if (!user) {
        return (
            <Box textAlign="center" py={10}>
                <Heading size="md">      </Heading>
            </Box>
        )
    }

    return (
        <Container maxW="container.md" py={8}>
            <Heading mb={6} textAlign="center">   </Heading>

            <Tabs isFitted variant="enclosed" colorScheme="brand">
                <TabList mb="1em">
                    <Tab> </Tab>
                    <Tab> </Tab>
                </TabList>
                <TabPanels>
                    <TabPanel>
                        <form onSubmit={handleTreatySubmit}>
                            <VStack spacing={4}>
                                <FormControl isRequired>
                                    <FormLabel> </FormLabel>
                                    <Input value={treaty.name} onChange={e => setTreaty({ ...treaty, name: e.target.value })} placeholder=":  " />
                                </FormControl>
                                <FormControl isRequired>
                                    <FormLabel></FormLabel>
                                    <Input type="number" value={treaty.date} onChange={e => setTreaty({ ...treaty, date: e.target.value })} placeholder="1949" />
                                </FormControl>
                                <FormControl>
                                    <FormLabel></FormLabel>
                                    <Select value={treaty.topic} onChange={e => setTreaty({ ...treaty, topic: e.target.value })}>
                                        <option value=" "> </option>
                                        <option value=" "> </option>
                                        <option value=" "> </option>
                                        <option value=""></option>
                                        <option value=" "> </option>
                                    </Select>
                                </FormControl>
                                <FormControl>
                                    <FormLabel>  </FormLabel>
                                    <Input value={treaty.states} onChange={e => setTreaty({ ...treaty, states: e.target.value })} placeholder=": 196 " />
                                </FormControl>
                                <FormControl isRequired>
                                    <FormLabel>  </FormLabel>
                                    <Input type="url" value={treaty.link} onChange={e => setTreaty({ ...treaty, link: e.target.value })} />
                                </FormControl>
                                <FormControl>
                                    <FormLabel></FormLabel>
                                    <Textarea value={treaty.description} onChange={e => setTreaty({ ...treaty, description: e.target.value })} />
                                </FormControl>
                                <Button type="submit" colorScheme="blue" width="100%" isLoading={loading}> </Button>
                            </VStack>
                        </form>
                    </TabPanel>

                    <TabPanel>
                        <form onSubmit={handleCaseSubmit}>
                            <VStack spacing={4}>
                                <FormControl isRequired>
                                    <FormLabel> </FormLabel>
                                    <Input value={caseData.name} onChange={e => setCaseData({ ...caseData, name: e.target.value })} placeholder=":   " />
                                </FormControl>
                                <FormControl isRequired>
                                    <FormLabel></FormLabel>
                                    <Input type="number" value={caseData.year} onChange={e => setCaseData({ ...caseData, year: e.target.value })} />
                                </FormControl>
                                <FormControl>
                                    <FormLabel></FormLabel>
                                    <Select value={caseData.court} onChange={e => setCaseData({ ...caseData, court: e.target.value })}>
                                        <option value="ICJ">   (ICJ)</option>
                                        <option value="ICC">  (ICC)</option>
                                        <option value="ECHR">  </option>
                                    </Select>
                                </FormControl>
                                <FormControl isRequired>
                                    <FormLabel> </FormLabel>
                                    <Input type="url" value={caseData.link} onChange={e => setCaseData({ ...caseData, link: e.target.value })} />
                                </FormControl>
                                <FormControl>
                                    <FormLabel></FormLabel>
                                    <Textarea value={caseData.summary} onChange={e => setCaseData({ ...caseData, summary: e.target.value })} />
                                </FormControl>
                                <Button type="submit" colorScheme="purple" width="100%" isLoading={loading}> </Button>
                            </VStack>
                        </form>
                    </TabPanel>
                </TabPanels>
            </Tabs>
        </Container>
    )
}
