import { useState } from 'react'
import {
    Box, Heading, Text, VStack, FormControl, FormLabel, Input, Textarea, Select,
    Button, useToast, Card, CardBody, Alert, AlertIcon, useColorModeValue
} from '@chakra-ui/react'
import { Link } from 'react-router-dom'
import { FiSend, FiArrowRight } from 'react-icons/fi'
import { submitContribution } from '../hooks'

const topics = [' ', ' ', ' ', ' ', '', ' ']

export function ContributePage() {
    const [type, setType] = useState('treaty')
    const [formData, setFormData] = useState({})
    const [loading, setLoading] = useState(false)
    const [submitted, setSubmitted] = useState(false)
    const toast = useToast()
    const cardBg = useColorModeValue('white', 'gray.800')

    const handleSubmit = async (e) => {
        e.preventDefault()
        setLoading(true)

        const tableMap = { treaty: 'treaties', case: 'cases', resolution: 'resolutions' }
        const { error } = await submitContribution(tableMap[type], formData)

        setLoading(false)

        if (error) {
            toast({
                title: ' ',
                description: error.message,
                status: 'error',
                duration: 5000,
            })
        } else {
            setSubmitted(true)
            toast({
                title: '  ',
                description: ' !   .',
                status: 'success',
                duration: 5000,
            })
        }
    }

    if (submitted) {
        return (
            <Box maxW="600px" mx="auto" p={8} textAlign="center">
                <Alert status="success" borderRadius="lg" mb={6}>
                    <AlertIcon />
                       !     .
                </Alert>
                <Button as={Link} to="/" colorScheme="brand" leftIcon={<FiArrowRight />}>
                     
                </Button>
            </Box>
        )
    }

    return (
        <Box maxW="700px" mx="auto" p={{ base: 4, md: 8 }}>
            <VStack align="stretch" spacing={6}>
                <Box>
                    <Heading size="lg" mb={2}>  </Heading>
                    <Text color="gray.500">        </Text>
                </Box>

                <Card bg={cardBg}>
                    <CardBody>
                        <form onSubmit={handleSubmit}>
                            <VStack spacing={4} align="stretch">
                                <FormControl isRequired>
                                    <FormLabel> </FormLabel>
                                    <Select value={type} onChange={(e) => { setType(e.target.value); setFormData({}); }}>
                                        <option value="treaty"> </option>
                                        <option value="case"> </option>
                                        <option value="resolution"> </option>
                                    </Select>
                                </FormControl>

                                {type === 'treaty' && (
                                    <>
                                        <FormControl isRequired>
                                            <FormLabel> </FormLabel>
                                            <Input placeholder=":    " onChange={(e) => setFormData({ ...formData, name: e.target.value })} />
                                        </FormControl>
                                        <FormControl>
                                            <FormLabel> </FormLabel>
                                            <Input type="number" placeholder="1961" onChange={(e) => setFormData({ ...formData, date: e.target.value })} />
                                        </FormControl>
                                        <FormControl>
                                            <FormLabel></FormLabel>
                                            <Select placeholder=" " onChange={(e) => setFormData({ ...formData, topic: e.target.value })}>
                                                {topics.map(t => <option key={t} value={t}>{t}</option>)}
                                            </Select>
                                        </FormControl>
                                        <FormControl>
                                            <FormLabel>  </FormLabel>
                                            <Input type="number" placeholder="193" onChange={(e) => setFormData({ ...formData, states: e.target.value })} />
                                        </FormControl>
                                        <FormControl>
                                            <FormLabel>  </FormLabel>
                                            <Input type="url" placeholder="https://..." onChange={(e) => setFormData({ ...formData, link: e.target.value })} />
                                        </FormControl>
                                        <FormControl>
                                            <FormLabel> </FormLabel>
                                            <Textarea placeholder="  ..." onChange={(e) => setFormData({ ...formData, description: e.target.value })} />
                                        </FormControl>
                                    </>
                                )}

                                {type === 'case' && (
                                    <>
                                        <FormControl isRequired>
                                            <FormLabel> </FormLabel>
                                            <Input placeholder=":    " onChange={(e) => setFormData({ ...formData, name: e.target.value })} />
                                        </FormControl>
                                        <FormControl>
                                            <FormLabel></FormLabel>
                                            <Select placeholder=" " onChange={(e) => setFormData({ ...formData, court: e.target.value })}>
                                                <option value="ICJ">   (ICJ)</option>
                                                <option value="ICC">   (ICC)</option>
                                                <option value="ECHR">   </option>
                                            </Select>
                                        </FormControl>
                                        <FormControl>
                                            <FormLabel></FormLabel>
                                            <Input type="number" placeholder="2024" onChange={(e) => setFormData({ ...formData, year: e.target.value })} />
                                        </FormControl>
                                        <FormControl>
                                            <FormLabel> </FormLabel>
                                            <Textarea placeholder=" ..." onChange={(e) => setFormData({ ...formData, summary: e.target.value })} />
                                        </FormControl>
                                        <FormControl>
                                            <FormLabel> </FormLabel>
                                            <Input type="url" placeholder="https://..." onChange={(e) => setFormData({ ...formData, link: e.target.value })} />
                                        </FormControl>
                                    </>
                                )}

                                {type === 'resolution' && (
                                    <>
                                        <FormControl isRequired>
                                            <FormLabel> </FormLabel>
                                            <Input placeholder=": S/RES/2728" onChange={(e) => setFormData({ ...formData, number: e.target.value })} />
                                        </FormControl>
                                        <FormControl>
                                            <FormLabel> </FormLabel>
                                            <Select placeholder=" " onChange={(e) => setFormData({ ...formData, type: e.target.value })}>
                                                <option value=" "> </option>
                                                <option value=" "> </option>
                                            </Select>
                                        </FormControl>
                                        <FormControl>
                                            <FormLabel></FormLabel>
                                            <Input type="number" placeholder="2024" onChange={(e) => setFormData({ ...formData, year: e.target.value })} />
                                        </FormControl>
                                        <FormControl>
                                            <FormLabel> </FormLabel>
                                            <Textarea placeholder=" ..." onChange={(e) => setFormData({ ...formData, subject: e.target.value })} />
                                        </FormControl>
                                        <FormControl>
                                            <FormLabel> </FormLabel>
                                            <Input type="url" placeholder="https://undocs.org/..." onChange={(e) => setFormData({ ...formData, link: e.target.value })} />
                                        </FormControl>
                                    </>
                                )}

                                <Button type="submit" colorScheme="brand" size="lg" leftIcon={<FiSend />} isLoading={loading}>
                                     
                                </Button>
                            </VStack>
                        </form>
                    </CardBody>
                </Card>
            </VStack>
        </Box>
    )
}
